.hero{
    display:flex;
    justify-content: space-between;
    
}
.left-h{
    padding:2rem;
    padding-top: 1.5rem;
    flex: 3;
    height: 45rem;
   
}

.right-h{
    flex: 1;
    position: relative;
    background-color: var(--orange);
    height: 44rem;
   
}

.the-best-ad{
    margin-top: 4rem;
    background-color: #363d42;
    border-radius: 4rem;
    width: fit-content;
    padding: 20px 13px;
    text-transform: uppercase;
    color:white;
    position: relative;
    display:flex;
    align-items: center;
    justify-content: flex-start;
}  

.the-best-ad>span{
    z-index: 2;
}

.the-best-ad>div{
    position: absolute;
    background-color: var(--orange);
    width: 5.4rem;
    height: 80%;
    left: 8px;
    border-radius: 3rem;
    z-index: 1;

}  

.hero-text{
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    text-transform: uppercase;
    font-size: 4.5rem;
    font-weight: bold;
    color: white;
    text-overflow: inherit;
}
.hhhh{
    font-size: 1rem;
    font-weight: 200;
    text-transform: none;
    letter-spacing: 1px;
    width: 80%;

}

.figures{
    display: flex;
    gap: 2rem;

}

.figures>div{
    display: flex;
    flex-direction: column;

}

.figures>div>span:nth-of-type(1){
    color: white;
    font-size: 2rem;



}

.figures>div>span:nth-of-type(2){
    color:  var(--gray);
   text-transform: uppercase;


}

.hero-btn{
    display: flex;
    gap: 1rem;
    position: relative;
    top: 25px;
    
    
    font-weight: normal;
}

.hero-btn>:nth-child(1){
    color: white;
    background-color:  var(--orange);
    width: 8rem;
    top: 5rem;
   
    



}
.hero-btn>:nth-child(2){
    color: white;
    background-color:  transparent;
    width: 8rem;
    border:  2px solid var(--orange);



}

.right-h>.btn{
    position: absolute;
    right: 3rem;
    top: 2rem;
    color: black;
}

.heart-rate{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    background-color: var(--darkGrey);
    width: fit-content;
    padding: 1rem;
    align-items: start;
    border-radius: 5px;
    position: absolute;
    right: 4rem;
    top: 7rem;
}
.heart-rate>span:nth-child(2){
    color: var(--gray);
}
.heart-rate>span:nth-child(3){
    color: white;
    font-size: 1.5rem;
    
    
}

.heart-rate>img{
    width: 2rem;
}

.hero-image{
    position: absolute;
    top: 10rem;
    right: 8rem;
    width: 23rem;

}

.hero-image-back{
    position: absolute;
    top: 4rem;
    right: 20rem;
    z-index: -1;
    width: 15rem;




}

.calories{
    display: flex;
    gap: 2rem;
    background-color:  var(--caloryCard);
    border-radius: 5px;
    padding: 1rem;
    width: fit-content;
    position: absolute;
    top: 400px;
    right: 500px;






}

.calories>img{
    width: 3rem;

}
.calories>div{
    display: flex;
    flex-direction: column ;
    justify-content: space-between;

}

.calories>div :nth-child(1){
    color: var(--gray);
}
.calories>div:nth-child(2){
    color: white;
    font-size: 1.5rem;


}








